import { Subject, throwError, from, merge, of } from "rxjs";
import { filter, map, sample, switchMap, mergeMap } from "rxjs/operators";

function getListState({ url, method='GET' }) {
  const getAjax = new XMLHttpRequest(method, url);
  getAjax.open("GET", `${url}`);
  getAjax.send();
  return new Promise(function(resolve, reject) {
    getAjax.onreadystatechange = () => {
      if (getAjax.readyState === 4) {
        if (getAjax.status === 200) {
          return resolve(
            of({
              CacheState: JSON.parse(getAjax.response).data
            })
          );
        } else {
          reject(throwError(""));
        }
      }
    };
  });
}
export default () => {
  const CacheObservable = new Subject();
  /*
  * URL
  * url
  * */
  const differentCache = CacheObservable.pipe(
    filter(({ thisId }) => thisId),
    filter(({ thisId, historyId }) => !Object.is(thisId, historyId)),
    map(({ thisId }) => {
      return {
        CacheId: thisId,
        CacheState: null
      };
    })
  );
  /*
  * @cacheState:
  * @selectCache
  * */
  const cacheState = CacheObservable.pipe(
    filter(({ type }) => type === "cacheState"),
    map(({ state }) => ({ ...state }))
  );
  const selectCache = CacheObservable.pipe(
    filter(({ type }) => type === "selectCache"),
    map(({ selectHistory }) => ({ selectHistory: selectHistory }))
  );
  /*
  * @getStateurl
  * */
  const getState = CacheObservable.pipe(
    sample(differentCache),
    mergeMap(({ requestOptions }) => from(getListState(requestOptions))),
    switchMap(value => value)
  );
  const outState = merge(cacheState, differentCache, getState, selectCache);
  return { putState: CacheObservable, outState: outState };
};



